import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";
import { ReanderField, ReanderSelect } from "../../../components/helpers/field";
import { NumberOnly } from "../../../Validasi/normalize";
import { TabContent, TabPane, Nav, NavItem, NavLink } from "reactstrap";
import classnames from "classnames";
import PerfectScrollbar from "react-perfect-scrollbar";
import { getItem, getUserdata } from "../../../components/helpers/function";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: " ,-",
  locale: "kr-KO",
});
const kategori_harga = [
  {
    value: "harga_100%",
    name: "HARGA 100%",
  },
  {
    value: "harga_per_gram",
    name: "HARGA / GRAM",
  },
];
const type_poin = [
  {
    value: "GRAM",
    name: "GRAM",
  },
  {
    value: "RP",
    name: "RP",
  },
];
class FormParameterPoint extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showMember: true,
      activeTab: "1",
    };
    this.toggle = this.toggle.bind(this);
  }
  toggle(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab,
      });
    }
  }
  componentDidMount() {
    // this.props.dispatch(getParaSystem());
  }

  render() {
    setTimeout(() => {
      this.props.DataSystem.forEach((element) => {
        if (element.key === "KATEGORI_HARGA") {
          this.props.change("kategori_harga", element.value);
        }
        if (element.key === "TIPE_POIN") {
          this.props.change("type_poin", element.value);
        }
        if (element.key === "CASHBACK_POIN") {
          this.props.change("cashback_poin", element.value);
        }
        if (element.key === "BOUNDARY_JENIS_GROUP") {
          this.props.change("kadar", element.value);
        }
        if (element.key === "SEMI_GROSIR_MODULE") {
          this.props.change("module_semi_grosir", JSON.parse(element.value));
        }
        if (element.key === "REPRINT_NOTA_HUTANG") {
          this.props.change("module_reprint_hutang", JSON.parse(element.value));
        }
        if (element.key === "BERLIAN_MODULE") {
          this.props.change("module_berlian", JSON.parse(element.value));
        }
        if (element.key === "SEPUHAN_MODULE") {
          this.props.change("sepuhan_module", JSON.parse(element.value));
        }
        if (element.key === "CURRENCY_DOLLAR_RUPIAH") {
          this.props.change("currency_dollar_rupiah", JSON.parse(element.value));
        }
      });
    }, 1000);
    const module = getItem('module') || [];
    let cek = module.findIndex((list)=> list.key === "BERLIAN_MODULE");
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <Nav
          tabs
          className="nav-tabs-inverse nav-justified nav-justified-mobile"
        >
          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === "1" })}
              onClick={() => {
                this.toggle("1");
              }}
            >
              <i className="fa fa-cog fa-lg m-r-5"></i>{" "}
              <span className="d-none d-md-inline">Setting System</span>
            </NavLink>
          </NavItem>
          {getUserdata().level === "SU" ?
          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === "2" })}
              onClick={() => {
                this.toggle("2");
              }}
            >
              <i className="fa fa-archive fa-lg m-r-5"></i>{" "}
              <span className="d-none d-md-inline">Module</span>
            </NavLink>
          </NavItem>
          : null}
        </Nav>
        <TabContent activeTab={this.state.activeTab}>
          <TabPane tabId="1">
            <PerfectScrollbar
              className="height-sm"
              options={{ suppressScrollX: true }}
            >
              <br></br>

              <div className="row">
                <div className="col-4">
                  <Field
                    name="kategori_harga"
                    label="Pilih Kategori Harga"
                    placeholder="Masukan Kategori Harga"
                    options={kategori_harga}
                    component={ReanderSelect}
                    // onChange={(e)=>this.props.gantiKadar(e)}
                  />
                </div>
                <div className="col-4">
                  <Field
                    name="type_poin"
                    label="Pilih Type Poin"
                    placeholder="Masukan Type Poin"
                    options={type_poin}
                    component={ReanderSelect}
                  />
                </div>
                <div className="col-4">
                  <Field
                    name="cashback_poin"
                    label="Cashback Poin"
                    placeholder="Masukan Cashback Poin"
                    text="text"
                    {...currencyMask}
                    component={ReanderField}
                  />
                </div>
                {this.props.kategoriharga ? (
                  <div className="col-4">
                    <Field
                      name="kadar"
                      label="Kadar"
                      placeholder="Masukan Kadar"
                      text="text"
                      normalize={NumberOnly}
                      component={ReanderField}
                    />
                  </div>
                ) : null}
                {cek === -1 ? null: 
                  <div className="col-4">
                    <Field
                      name="currency_dollar_rupiah"
                      label="Parameter Curency"
                      placeholder="Masukan Curency"
                      text="text"
                      {...currencyMask}
                      normalize={NumberOnly}
                      component={ReanderField}
                    />
                  </div>
                }

                <div className="col-12 text-right">
                  <label> &nbsp;</label>
                  <button
                    className="btn btn-primary"
                    disabled={this.props.isLoading}
                    type="submit"
                  >
                    {this.props.isLoading ? (
                      <>
                        <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                        Menyimpan
                      </>
                    ) : (
                      "Simpan Data"
                    )}
                  </button>
                </div>
              </div>
            </PerfectScrollbar>
          </TabPane>
          <TabPane tabId="2">
            <PerfectScrollbar
              className="height-sm"
              options={{ suppressScrollX: true }}
            >
              <div className="row">
                <div className="col-6">
                  <div className="form-group">
                    <label>
                      <Field
                        id="semi_grosir"
                        name="module_semi_grosir"
                        component="input"
                        type="checkbox"
                      />{" "}
                      Module Semi Grosir
                    </label>
                    <br/>
                    <label>
                      <Field
                        id="module_reprint_hutang"
                        name="module_reprint_hutang"
                        component="input"
                        type="checkbox"
                      />{" "}
                      Module Reprint Priviw Hutang
                    </label>
                    <br/>
                    <label>
                      <Field
                        id="module_berlian"
                        name="module_berlian"
                        component="input"
                        type="checkbox"
                      />{" "}
                      Module Berlian
                    </label>
                    <br/>
                    <label>
                      <Field
                        id="sepuhan_module"
                        name="sepuhan_module"
                        component="input"
                        type="checkbox"
                      />{" "}
                      Module Speuhan
                    </label>
                  </div>
                 
                </div>
                <div className="col-6">
                  
                </div>
                
                  <div className="col-12 text-right">
                    <label> &nbsp;</label>
                    <button
                      className="btn btn-primary"
                      disabled={this.props.isLoading}
                      type="submit"
                    >
                      {this.props.isLoading ? (
                        <>
                          <i className="fas fa-spinner fa-spin"></i> &nbsp;
                          Sedang Menyimpan
                        </>
                      ) : (
                        "Simpan Data"
                      )}
                    </button>
                  </div>
              </div>
            </PerfectScrollbar>
          </TabPane>
        </TabContent>
      </form>
    );
  }
}

FormParameterPoint = reduxForm({
  form: "FormParameterPoint",
  enableReinitialize: true,
})(FormParameterPoint);
export default connect()(FormParameterPoint);
